import React from 'react'
import { Link } from 'react-router-dom'

export function ItemPreview({ item, url }) {
    console.log(item);
    return (
        <div className="item-card">
            <div className="flex align-center justify-center ">
                <div className="item-img"><img src={url} alt="#" /></div>
                <div className="brown-line"></div>
                <div className="flex column item-text">
                    <h4> {item.title}</h4>
                    <p>{item.desc}</p>
                </div>
            </div>
        </div>
    )
}