import React from 'react'
import { ItemPreview } from './ItemPreview'
import { Card } from '../Card'

export function ItemList({ items }) {

    return (
        <div className= "flex column item-list">
            {
                items.map((item,idx)=>
                <ItemPreview key={idx} item={ item } url={item.imgUrl}
                />)
            }
        </div>
    )
}
