import React, { Component } from "react";
import { ItemList } from '../../cmps/item/ItemList';
import { itemService } from '../../services/itemService.js'
import { Filter } from '../../cmps/item/Filter';

export class ItemApp extends Component {

    state = {
        items: null,
        LocationKey: null
    }

    componentDidMount() {
        window.scrollTo(0, 0);
        this.loadItems()
    }

    async componentDidUpdate(prevProps) {
        if (!prevProps.filterBy) return;
        if (prevProps.filter !== this.props.filterBy) this.loadItems()
    }

    loadItems = async () => {
        try {
            const items = await itemService.query();
            this.setState({ items })
        }
        catch {
            console.log('ERROR WHILE LOADING ITEMS');
        }
    }

    // onSetFilter = (filterBy) => {
    //     this.setState({ filterBy });
    // };


    // onRemove = async (_id) => {
    //     try{
    //     await itemService.removeItem(_id);
    //     this.loadItems()
    //     }
    //     catch{
    //         console.log('ERROR WHILE TRYING TO REMOVE THE ITEM');
    //     }
    // };


    render() {
        const { items } = this.state;
        return (
            <div className="item-container flex ">
                <div className="flex column sb item-left">
                    <div className="left-item-img"><img src="https://res.cloudinary.com/dygtul5wx/image/upload/v1616411640/job-interview%20pixle%20perfect/pattern-divide_ij02lc.png"/></div>
                    <h3>A few highlights from our menu</h3>
                    <p>  We cater for all dietary requirements, but hereâ€™s a glimpse at some of our dinerâ€™s favourites.
                    Our menu is revamped every season.</p>
                </div>
                {(items) ? <div className="item-right">
                    <ItemList bottomBorder={true} items={items} />
                </div> : ''}
            </div>
        )
    }
}

